.docs-config-editor {
    display: flex;

    > div {
        align-items: flex-start;
        margin-left: 2%;
        width: 25%;

        &:first-child {
            margin-left: 0;
        }
    }

    button {
        font-size: 13px;
        border-radius: 3px;
    }

    .docs-config-editor-objects {
        .label {
            max-width: calc(100% - 85px);
        }
    }

    .docs-config-editor-elements {
        .label {
            max-width: calc(100% - 175px);
        }
    }

    .docs-config-editor-element {
        width: 45%;

        #elementContentShort {
            min-height: 10vh;
        }

        #elementContentFull {
            min-height: 50vh;
        }
    }

    .edit-bar-content {
        padding-bottom: 0;

        button {
            margin-top: 0;
        }
    }

    div.List,
    ul {
        list-style: none;
        margin-left: 0;
        padding-left: 0;

        .draggable,
        li {
            display: block;
            clear: both;
            cursor: pointer;
            padding: 5px 0 5px 5px;
            font-size: 14px;

            > span {
                display: block;
            }

            &:nth-child(odd) {
                background: #eee;
            }
            // &.active {
            //     color: $greensock-green-dark;
            //     font-weight: bold;
            // }
            .label {
                display: inline-block;
                max-width: calc(100% - 85px);
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            .note {
                clear: both;
                font-size: 10px;
            }
        }

        .placeholderContent {}

        .active {
            color: $greensock-green-dark;
            font-weight: bold;
        }

        .dragged {
            opacity: 0.7;
        }

        .placeholder {
            opacity: 0.5;
        }
    }
}


