@import 'vars';

.docs-head-container {
    height: auto;

    .breadcrumbs {
        padding: 14px 0;
    }

    .right {
        float: right;
    }
}

.docs-main-container {
    .docs-main {
        opacity: 1;
        visibility: visible;

        .docs-nav .panels .side-container {
            position: relative;

            .side .nav-panels .inner-panel {
                .item {
                    transition: 0.3s ease-in-out;

                    p {
                        transition: 0.3s ease-in-out;
                    }
                }

                .item-class,
                .item-element,
                .item-folder,
                .item-page {
                    &:hover {
                        background-color: #f8f8f8;

                        &:before {
                            width: 10px;
                        }

                        p {
                            transform: translate3d(10px, 0, 0);
                        }
                    }
                }

                .item-element {
                    &:hover {
                        background-color: #3b3b3b;
                    }
                }

                .item-class {
                    &:before {
                        content: '';
                        display: inline-block;
                        width: 3px;
                        height: 100%;
                        background: $greensock-green;
                        position: absolute;
                        top: 0;
                        left: 0;
                        transition: 0.3s ease-in-out;
                    }
                }

                .item-folder {
                    &:before {
                        background: none;
                    }
                }

                .item-header {
                    height: auto;

                    p {
                        font-size: 1.1em;
                        font-weight: bold;
                        padding: 0.5em 30px;
                    }
                }

                .item-page {
                    &:before {
                        content: '';
                        display: inline-block;
                        width: 3px;
                        height: 100%;
                        background: orange;
                        position: absolute;
                        top: 0;
                        left: 0;
                        transition: 0.3s ease-in-out;
                    }
                }

                .doc-element {
                    display: block;

                    a {
                        display: block;
                        text-decoration: none;
                    }
                }
            }
        }

        .docs-content {
            h1,
            h4 {
                a {
                    color: $dark-gray;
                    text-decoration: none;
                }
            }

            .docs-feature {
                display: flex;

                .left {
                    width: auto;
                    padding-right: 40px;
                }

                .right {
                    width: auto;
                    padding-left: 40px;
                }
            }

            .class-intro-text {
                height: auto;
            }

            .show-more-line {
                .show-more-intro {
                    display: none;
                }

                .fade {
                    display: none;
                }
            }

            .element-table {
                .elements {
                    > div.element:first-child {
                        border: none;
                    }

                    .config-object {
                        // overflow: hidden;
                        .config-object-props {
                            height: auto;
                            // overflow: visible;
                        }

                        .show-more {
                            transition: background-color 0.3s;

                            &:hover {
                                background-color: #777;
                            }
                        }
                    }

                    .show-more-container {
                        .fade {
                            transition: opacity 1s;
                        }

                        &.opened {
                            .fade {
                                opacity: 0;
                            }
                        }

                        &.closed {}
                    }
                }
            }
        }
    }

    .divider-line {
        width: percentage(820 / 830);
        height: 2px;
    }
}

.config-object,
.config-object-shortcode {
    padding-left: 1em !important;

    .config-object-props {
        padding: 0 0 0 1em;

        .prop {
            span p {
                display: inline;
            }
        }

        .prop-with-more {
            .short {
                display: inline;
            }

            .long {
                display: none;
            }

            .more-less {
                cursor: pointer;

                &:before {
                    content: '+';
                    display: inline-block;
                    cursor: pointer;
                    margin-left: -1em;
                    min-width: 1em;
                }
            }

            &.open {
                .short {
                    display: none;
                }

                .long {
                    display: inline;
                }

                .more-less {
                    &:before {
                        content: '\2013';
                    }
                }
            }
        }
    }
}
@import 'docs-v2';
@import 'docs-v2-editor';
@import 'docs-config-editor'


