.panel-rotator {
    position: relative;

    .panel {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
    }
}

.panel-shifter {
    position: relative;
    // overflow: hidden;
    .slide {
        position: absolute;
        top: 0;
        left: 0;
    }
}

.panel-rotator {
    // min-height: 300px;
    .panel {
        min-height: 100%;
    }

    .panel-shifter {
        min-height: 100%;
    }
}
//removing width constraints

x.docs-main-container {
    .docs-main {
        width: auto;
        display: flex;

        > div {
            margin: 0 20px;
        }

        .docs-nav {
            min-width: 300px;
        }

        .docs-content {
            width: auto;
        }
    }
}

.breadcrumbs {
    &.right {
        float: right;
    }
}

.docs-nav {
    .panel-rotator {
        .panel {
            width: calc(100% - 2px);
            border: 1px solid #ccc;
            border-top: none;
            border-bottom: none;
            background: #fff;
        }

        &:after {
            content: '';
            display: block;
            position: absolute;
            bottom: -15px;
            left: 0;
            width: calc(100% - 2px);
            height: 15px;
            background: #fff;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            border: 1px solid #ccc;
            border-top: none;
        }
    }

    .panel-shifter {
        .slide {
            width: 100%;
            border-right: 1px solid #ccc;
        }
    }

    .group-header {
        display: block;
        // height: 56px;
        position: relative;
        background: #fff;
        border-bottom: 1px solid #e4e4e4;

        p {
            display: inline-block;
            color: $mid-gray;
            font-family: $Signika;
            font-weight: $bold;
            font-size: 16px;
            padding: 20px 0 20px 3em;
            transition: 0.3s ease-in-out;
        }

        &.back {
            &:before {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                background: url('https://greensock.com/wp-content/themes/greensock/images/docs/back-arrow.jpg') no-repeat;
                background-size: 34px 56px;
                width: 34px;
                height: 100%;
                transform: translateY(-50%);
            }
        }

        &:hover {
            p {
                color: $greensock-green;
            }
        }
    }

    .item {
        position: relative;
        display: block;
        width: 100%;
        // height: 46px;
        border-bottom: 1px solid #e4e4e4;
        background: #ffffff;
        text-decoration: none;

        p {
            display: inline-block;
            color: #777;
            font-family: $Signika;
            font-weight: 400;
            font-size: 14px;
            padding: 15px 0 15px 30px;
            transition: 0.3s ease-in-out;
        }

        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            background: url('https://greensock.com/wp-content/themes/greensock/images/docs/item-arrow.png') no-repeat;
            background-size: 45px 44px;
            width: 45px;
            height: 44px; //100%;
            transform: translateY(-50%);
        }

        .b {
            display: inline-block;
            float: right;
            font-family: $OpenSans;
            font-size: 11px;
            color: #777;
            font-weight: $bold;
            // height: 13px;
            min-width: 20px;
            background: #ededed;
            border-radius: 14px;
            padding: 4px 11px 5px;
            margin: 10px 43px 0 0;
            text-align: center;
            border: 1px solid #d5d5d5;
            box-shadow: 1px 1px 0 rgba(255, 255, 255, 1);
            text-shadow: 1px 1px 0 rgba(255, 255, 255, .6);
        }

        &.item-class {
            // height: 56px;
            p {
                color: $mid-gray;
                font-size: 16px;
                font-weight: $bold;
                padding: 20px 0 19px 30px;
            }

            &:before {
                content: '';
                display: inline-block;
                width: 3px;
                height: 100%;
                background: $greensock-green;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.3s ease-in-out;
            }
        }

        &.item-page {
            p {}

            &:before {
                content: '';
                display: inline-block;
                width: 3px;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                transition: 0.3s ease-in-out;
            }

            &:after {
                display: none;
            }

            &.active {
                p {
                    color: $greensock-green;
                    font-weight: bold;
                }
            }
        }

        &.item-info {
            // height: auto;
            p {
                font-weight: bold;
            }

            &:after {
                display: none;
                padding: 0.5em;
            }
        }

        &.item-page {
            p {
                font-weight: bold;
                font-size: 18px;
            }
        }

        &.item-element {
            // height: 25px;
            border: none;
            background: #636363;
            padding: 3px 0;
            transition: 0.3s ease-in-out;

            p {
                color: #e1e1e1;
                font-family: $Signika;
                font-weight: 300;
                font-size: 15px;
                padding: 4px 0 4px 30px;
                transition: 0.3s ease-in-out;

                span {
                    color: #a4a4a4;
                }
            }

            &:after {
                display: none;
            }

            &:hover {
                background: #3b3b3b;

                p {
                    color: #fff;
                    padding-left: 35px;
                }
            }

            &.active {
                background: $greensock-green-bright !important;

                p {
                    color: #232323;
                    text-shadow: none;

                    span {
                        color: #232323;
                    }
                }
            }
        }

        &.item-header {
            // height: auto;
            p {
                font-size: 1.1em;
                font-weight: bold;
                padding: 0.5em 30px;
            }
        }

        &.item-class,
        &.item-folder,
        &.item-page {
            &:hover {
                background-color: #f8f8f8;

                p {
                    padding-left: 35px;
                    color: $greensock-green;
                }

                &:before {
                    width: 8px;
                }
            }
        }
    }

    .group {
        overflow: hidden;

        .group-header {
            // height: 44px;
            width: 300px;
            border-top: 1px solid #bcbcbc;
            position: relative;
            background: #dedede;

            p {
                display: inline-block;
                color: $mid-gray;
                font-family: $Signika;
                font-weight: 300;
                font-size: 16px;
                padding: 13px 0 12px 25px;
                text-shadow: 1px 1px 0 rgba(255, 255, 255, .6);
            }

            .minus,
            .plus {
                position: absolute;
                top: 0;
                right: 0;
                width: 44px;
                cursor: pointer;
            }
        }

        .spacer {
            height: 4px;
            border: none;
            background: #636363;
        }
    }
}

.docs-content {
    a {
        text-decoration: none;
        color: $greensock-green;
        font-weight: 600;
    }

    .pre-container {
        position: relative;
        width: 830px;
        overflow: auto;
        overflow-y: hidden !important;
        margin-bottom: 16px !important;
        margin-top: 8px !important;
        background-color: #262626;
        border-radius: 6px;
        min-height: 15px;
    }

    .misc-version {
        font-size: 12px;
        color: $mid-gray;

        &.misc-version-element {
            padding-bottom: 12px;
        }
    }
}

.docs-download {
    display: inline-block;
    float: right;
    padding: 14px 0;

    a {
        font-size: 12px;
        color: $mid-gray;
        // line-height: 20px;
        text-decoration: none;
    }

    .icon {
        display: inline-block;
        width: 20px;
        margin-right: 0.5em;
        vertical-align: middle;
    }

    .label {
        display: inline-block;
        vertical-align: middle;
    }
}


