.edit-bar {
    display: inline-block;
    width: 100%;
    font-size: 14px;

    button {
        margin: 0 0 0 15px;
        font-size: 13px;
        cursor: pointer;
        outline: none;
        transition: color 0.3s, border-color 0.6s;
        border: 1px solid #ccc;
        background: #fff;
        color: #666;
        border-radius: 3px;

        &:hover {
            color: $greensock-green;
            border-color: $greensock-green;
        }

        &:first-child {
            margin-left: 0;
        }
    }

    select {
        font-size: 12px;
        outline: none;
        color: #666;
        border: none;
        background: #fff;
    }

    &.ontop {
        padding: 0 0 20px;
        margin: 0 0 20px;
        border-bottom: 1px solid #ccc;
    }

    &.float {
        display: inline-block;
        background: none;
        border: none;
    }

    .title {
        font-size: 14px;
        color: #666;
        line-height: 1em;
        padding-left: 5px;
        padding-bottom: 5px;
    }

    .buttons {
        display: flex;
        justify-content: center;
        padding: 5px;
    }

    .note {
        display: block;
        font-size: 11px;
    }

    .left {
        float: left;
    }

    .right {
        float: right;
    }
}

.edit-bar-breadcrumbs {
    padding: 14px 20px;
    width: auto;
}

.edit-bar-main {
    padding: 12px 0;
    display: flex;
    justify-content: space-between;

    > div {
        .buttons {
            padding-left: 0;
        }
    }

    .right {
        text-align: right;

        .buttons {
            padding-right: 0;
        }
    }
}

.edit-bar-content {
    @extend .ontop;

    .buttons {
        justify-content: space-between;
    }
}

.edit-bar.edit-bar-nav {
    padding: 10px 5px;
    background: #fff;
    border: 1px solid #ccc;
    border-top: none;
    width: calc(100% - 12px); // - padding - border

    button {
        margin: 0 5px;
    }
}

.edit-bar.edit-bar-nav-item {
    padding: 0 5px 10px;

    .buttons {
        justify-content: flex-start;
    }

    button {
        margin: 0 5px;
    }
}

.edit-bar.edit-bar-nav-item-element {
    // padding-top: 5px;
    // border-bottom: 1px solid #ccc;
    // button {
    //     background: #333;
    //     border-color: #999;
    //     color: #ccc;
    // }
}

.edit-bar.edit-bar-nav-group {
    @extend .float;
    float: right;
    margin-right: 45px;
    z-index: 111;
    width: auto;

    .buttons {
        justify-content: flex-end;
    }
}

.edit-bar.edit-bar-config-item {
    float: right;
    clear: both;
    padding-left: 10px;
    width: auto;

    button {
        margin: 0 5px 5px 0;
    }
}

.edit-status {
    .note {
        float: left;
        line-height: 23px;
        white-space: nowrap;
        padding: 0 10px;
        color: #fff;
        border-radius: 3px;
    }

    .edit-level-unknown .note {
        background: orange;
    }

    .edit-level-good .note {
        background: green;
    }

    .edit-level-bad .note {
        background: red;
    }
}

