}

.reactOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .75);
    z-index: 1000;
}

.ReactModal__Body--open {
    position: fixed;
    width: 100%;
    height: 100%;
}

.reactModal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 10px;
    outline: none;
    min-width: 30%;
    max-width: 90%;
    min-height: 10rem;
    max-height: 90%;
    padding: 20px;
    overflow: scroll;

    &.wide {
        min-width: 90%;
    }

    &.tall {
        min-height: 90%;
    }

    h2 {
        margin-bottom: 1em;
    }

    input,
    textarea:not(.fr-code) {
        display: block;
        font-size: 16px;
        margin: 10px 0;
        border: 1px solid #000;
        padding: 0.25em;
        outline: none;
        width: calc(100% - 0.5em);
    }

    textarea {
        min-height: 10vh;
    }

    button {
        font-size: 14px;
        border-radius: 0;
        background: #fff;
        border: 1px solid #ccc;
        margin: 10px 10px 10px 0;
        outline: none;
        cursor: pointer;
        transition: color 0.3s;

        &.close {
            position: absolute;
            top: 0;
            right: 10px;
            font-size: 40px;
            border: none;
            line-height: 40px;
        }

        &:hover {
            color: $greensock-green;
        }
    }

    .note {
        font-size: 0.8em;
    }
}
