.froala-editor.docs-main-container {
    display: inline-block;
    width: 100%;
    padding: 0;

    .docs-content {
        width: 100%;
        padding: 0;

        .fr-wrapper {
            background: #ededed;
        }
    }

    .fr-video {
        position: relative;
    }

    .atwho-inserted {
        outline: 1px solid orange;
        padding: 0 3px;
    }

    button[data-cmd="<code > "],
    button[data-cmd="@Link"],
    button[data-cmd="Config Objects"],
    button[data-cmd="PrettyPrint"],
    button[data-cmd="code"] {
        width: auto;
        padding: 0 10px;
    }

    button[data-cmd="Config Objects"] {
        padding-right: 20px;
    }
}

.fr-popup .fr-link-insert-layer label {
    // fix for overlapping label bars with white bgs http://tinyurl.com/ycljaqw7
    line-height: 0;
}


